const battleCalculator = function () {
    let battleResults = []
    const level = levelMap[this.$route.params.tier]
    const target = {
        stats: calculateStats(this.pokemon, level),
        types: this.pokemon.types
    }
    for (let p in this.fetchedData) {

        if (this.fetchedData[p].spreads !== undefined) {
            this.fetchedData[p].spread = this.fetchedData[p].spreads[0][0]

            const targeter = {
                stats: calculateStats(this.fetchedData[p], level),
                types: this.fetchedData[p].types
            }

            for (const move of this.fetchedData[p].moves) {
                if (classMap[move.category]) {
                    const damageTaken = damageCalculator(target, targeter, move, level);
                    battleResults.push({
                        damageTaken: damageTaken,
                        attacker: p,
                        move: move.name
                    })
                }
            }
        }
    }

    return battleResults.sort((a,b)=>{
        return b.damageTaken - a.damageTaken
    })
}

export default {
    battleCalculator
}