<template>
  <section>
    <div class='dexview__entry__spread' v-for='(spread, key) in data.spreads' :key='key'>
      <div class='dexview__entry__spread__nature'>
        <p>#{{key+1}} {{spread[0].nature}}</p>
      </div>
      <div class='dexview__entry__spread__stats'>
        <div
          class='dexview__entry__spread__stats__value'
          v-for='(stat, key2) in spread[0].stats'
          :key='key2'
        >
          <p>{{stat}}</p>
          <p class='dexview__entry__spread__stats__value--small'>{{alphabet[key2]}}</p>
        </div>
      </div>
      <button class='dexview__entry__spread__btn' @click='selectSpread(key)'></button>
    </div>

    <transition name='bounce'>
      <Modal
        v-if='battleCounter'
        transition='bounce'
        @close-my-modal='battleCounter = false'
        :sizeClass='"modal__content--large"'
      >
        <BattleSpread :pokemon='selectedSpreadData' />
      </Modal>
    </transition>
  </section>
</template>

<script>
import Modal from "@/components/Modal";
import BattleSpread from "@/components/BattleSpread";

export default {
  components: {
    Modal,
    BattleSpread
  },
  props: ["data"],
  data() {
    return {
      selectedSpreadData: {},
      battleCounter: false,
      alphabet: ["hp", "atk", "def", "spa", "spd", "spe"]
    };
  },
  methods: {
    selectSpread(spreadKey) {
      let pkmn = JSON.parse(JSON.stringify(this.data));
      pkmn.spread = pkmn.spreads[spreadKey][0];
      this.selectedSpreadData = pkmn;
      this.battleCounter = true;
    }
  }
};
</script>